.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #2b2b2b;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.HAHA {
  width: 20%;
  padding: 1px;
  margin: 1px;
  color: ivory;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 50px;
  font-weight: 'bold';
}

.App-link {
  color: #61dafb;
}

.join_button{
  font-family: Work Sans, sans-serif;
  margin: 10px;
  padding: 10px;
  color: ivory;
  background-color: #5271A0;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 'bold';
}
.join_button_2{
  font-family: Work Sans, sans-serif;
  margin: 10px;
  padding: 10px;
  color: ivory;
  background-color: #e1b54f;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 'bold';
}




.sign {
  padding: 10px;
  margin: 10px;
  text-align: center;
  border:none;
  background-color: #2b2b2b;
  color: ivory;
  border-radius: 10px;
  font-size: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
